@import url(https://fonts.googleapis.com/css2?family=Lato&display=swap);
*{
  margin: 0;
  padding: 0;
}

body {
  background-color: black;
  font-family: -apple-system, BlinkMacSystemFont, 'Segoe UI', 'Roboto', 'Oxygen',
    'Ubuntu', 'Cantarell', 'Fira Sans', 'Droid Sans', 'Helvetica Neue',
    sans-serif;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
}


.App {
  margin: 0;
  padding: 0;
}

.header{
  padding: 15px 0 5px 0;
  background: white;
}

.header--logo{
  font-size: 20px;
  color: #988345;
  font-weight: 700;
  padding-left: 20px;
}

.header--navbar{
  display: flex;
  justify-content: flex-start;
  list-style: none;
}

.header--navbar-item {
  margin-left: 2.3rem;
  color: rgb(92, 89, 89);
  font-weight: 350;
  font-size: 20px;
}

.first-item{
  margin-left: 7.5rem;
}

.header--navbar-item:hover {
  color: black;
  cursor: pointer;
}

@media screen and (max-width: 512px){
  .header--navbar{
    display: none;
  }
}
.intro{
    background-color: #bfdbd2;
    text-align: center;
}

.intro--symbol{
    margin-top: 5rem;
}

.intro--name{
    font-size: 6rem;
    margin: 0;
    font-family: 'Lato', sans-serif;
    letter-spacing: 0.3rem;
    padding-left: 40px;
    color: #333333;
}

.intro--line{
    width:70px;
    height: 5px;
    background-color: #333333;
    position: absolute;
    left: 47%;
}

.intro--roles{
    font-size: 1.5rem;
    letter-spacing: 0.1rem;
    color: #333333;
    margin: 40px 0 60px 90px;
    font-weight: 500;
}

.about{
    background-color:#ECC7C0;
}

#myImage{
    margin: 0 0 5rem 2rem;
    height: 420px;
    width: 380px;
    border-radius: 20px;
}

.about--heading{
    font-size: 3.5rem;
    font-family: 'Lora', sans-serif;
    margin: 5rem 0 3rem 0 ;
    color: #000000;
    font-weight: 800;
    text-align: center;
}

.about--para-1,
.about--para-2{
    color: black;
    font-size: 1.4rem;
    font-weight: 400;
    font-style: normal;
    line-height: 1.4;
    text-align: left;
}

.about--para-1{
    margin: 0 0 2rem 0;
}

#rutgers{
    text-decoration: none;
    color: rgb(45, 75, 204);
}

#rutgers:hover{
    color: black;
}

.about--para-2{
    text-align: left;

}

.about--listOfTech{
    display: flex;
    font-size: 1.3rem;
    margin-top: 10px;
}

.about--listOfTech_list1, 
.about--listOfTech_list2{
    list-style-type: '‣';
}

.about--listOfTech_list1{
    margin-right: 60px;
}

.experience{
    background-color: #8BCBC8;
    color: black;
}

.experience--heading{
    padding: 2.5rem 0;
    font-size: 3.5rem;
    font-family: 'Lora', sans-serif;
    margin: 0;
}

#att-img{
    border-radius: 100px;
    width: 250px;
    height: 120px;
    margin: 7rem;
}

.my-exp{
    padding-top: 2rem;
}
.experience1--des-1{
    padding-top: 2rem;
}
.experience--des-1, .experience--des-2, .experience--des-3{
    padding-bottom: 2rem;
    margin: 0;
    font-weight: 500;
    font-size: 1.1rem;
    text-align: left;
}
.contact {
    background-color: #4E3751;
    padding: 0 8rem;
    text-align: center;
}

.contact--heading{
    color: #ecc7c0;
    font-size: 3.5rem;
    margin: 2.5rem 0 1rem 0; 
    font-family: 'Lora', sans-serif;
}

.contact--text{
    font-size: 1.33rem;
    font-weight: 300;
    color: white;
    margin: 2rem 0;
}

.react-icons{
    margin: 20px 15px;
}

@media screen and (max-width: 700px){
    .contact{
        padding: 0 1rem;
    }
}
